import React from 'react'
import "./Loading.css"



const Loading = ({ title = "Loading...", visible }) => {
    if (!visible) {
        return ("")
    }

    return (
        <div className="outer" >
            <div className="card" >
                <div className="loader"></div>
                <h3 >{title}</h3>
            </div>
            <div className="blank" ></div>
        </div>
    )
}
export default Loading
