import React, { useState } from 'react'
import Textbox from "../../components/Textbox/Textbox"
import Button from "../../components/Button/Button"
import Loading from "../../components/Loading/Loading"
import { quizFB, videoFB } from '../../firebaseDatabase'


const CreateLession = () => {





    const [textData, setTextData] = useState({
        logoI: "",
        cTime: "",
        lessionN: "",
        quizMSG1: "",
        quizMSG2: "",
        quizMSG3: "",
        quizMSG4: "",
        loading: true,
        data: ""

    });



    const textHand = (e) => {
        setTextData((prevData) => {

            return {
                ...prevData,
                [e.target.name]: e.target.value,
            }
        })
    }


    if (textData.loading) {
        videoFB.ref('Courses/1/TL').on('value', (snapshot) => {


            setTextData((prevData) => {

                return {
                    ...prevData,
                    loading: false,
                    data: parseInt(snapshot.val()) + 2,

                }
            })
        });

    }

    const submit = () => {


        if (textData.cTime && textData.lessionN && textData.quizMSG2 && textData.quizMSG3 && textData.quizMSG4 && textData.logoI && textData.quizMSG1) {
            let countt = textData.data - 1
            videoFB.ref('Courses/1').update({
                TL: countt,
            }
            )
            videoFB.ref('Courses/1/Lessions/' + textData.data + '/').update({

                Icon: textData.logoI,
                Title: textData.lessionN,
                Time: textData.cTime,

            }
            );
            quizFB.ref('Courses/1/' + textData.data + '/').update({
                result: "[" + textData.quizMSG1 + "," + textData.quizMSG2 + "," + textData.quizMSG3 + "," + textData.quizMSG4 + "]",
            });


            setTextData({


                logoI: "",
                cTime: "",
                lessionN: "",
                quizMSG1: "",
                quizMSG2: "",
                quizMSG3: "",
                quizMSG4: "",

            }
            )
            console.log(textData.data)

        } else {
            alert("Enter all details")
        }
    }




    return (
        <div className="primary-container">
            <Loading title="Loading..." visible={textData.loading} />
            <Textbox onChange={textHand} value={textData.logoI} placeholder="Logo Icon" type="text" name="logoI" />
            <Textbox onChange={textHand} value={textData.lessionN} placeholder="Lession Name" type="text" name="lessionN" />
            <Textbox onChange={textHand} value={textData.cTime} placeholder="Completion Time" type="text" name="cTime" />
            <Textbox onChange={textHand} value={textData.quizMSG1} placeholder="Quiz MSG 1" type="text" name="quizMSG1" />
            <Textbox onChange={textHand} value={textData.quizMSG2} placeholder="Quiz MSG 2" type="text" name="quizMSG2" />
            <Textbox onChange={textHand} value={textData.quizMSG3} placeholder="Quiz MSG 3" type="text" name="quizMSG3" />
            <Textbox onChange={textHand} value={textData.quizMSG4} placeholder="Quiz MSG 4" type="text" name="quizMSG4" />
            <br />    <Button text="Submit" onClick={submit} />

        </div>
    )
}

export default CreateLession
