import React from 'react'
import { useState } from 'react'
import Button from '../../components/Button/Button'
import Textbox from '../../components/Textbox/Textbox'
import { videoFB } from '../../firebaseDatabase'


import "./Video.css"
import Loading from '../../components/Loading/Loading'
const Video = () => {

    const [textData, setTextData] = useState({

        lession: "",
        title: "",
        videoLink: "",
        count: "",
        loadingV: false

    });
    const textHand = (e) => {
        setTextData((prevData) => {
            return {
                ...prevData,
                [e.target.name]: e.target.value,
            }
        })
    }



    let data = ""
    const getVideoCount = () => {
        if (textData.lession) {
            setTextData((prevData) => {

                return {
                    ...prevData,
                    loadingV: true,
                }
            })

            videoFB.ref('Courses/1/Lessions/' + textData.lession + '/TV').on('value', (snapshot) => {
                // data = parseInt(snapshot.val().replace('"', "").replace('"', "")) + 1
                data = snapshot.val()

                if (isNaN(data)) {
                    while (data.includes('"')) {
                        data = data.replace('"', "")

                    }
                }
                data = parseInt(data) + 1

                setTextData((prevData) => {

                    return {
                        ...prevData,
                        count: data,
                        loadingV: false,
                    }
                })
            });

        } else {
            alert("Enter lession ID")
        }
    }

    const submit = () => {
        if (textData.lession && textData.count && textData.title && textData.videoLink) {
            let url = textData.videoLink
            while (url.includes("/")) {
                url = url.replace(':', "<>").replace('/', "()")

            }


            let data = {
                Title: textData.title,
                Url: url,
            }
            videoFB.ref('/Courses/1/Lessions/' + textData.lession + '/Videos/' + textData.count).update(data);
            videoFB.ref('Courses/1/Lessions/' + textData.lession).update({
                TV: textData.count,
            });
            setTextData((prevData) => {

                return {
                    ...prevData,
                    title: "",
                    videoLink: "",
                }
            })

        } else { alert("Enter all detail"); }
    }

    return (
        <div className="primary-container" >

            <Textbox placeholder="Lession ID" onChange={textHand} value={textData.lession} name="lession" type="number" />
            <Button text="Get Video Count" onClick={getVideoCount} /> <br />
            <h4>{textData.count}</h4>
            <Textbox placeholder="Title" onChange={textHand} value={textData.title} name="title" type="text" />
            <Loading visible={textData.loadingV} />
            <Textbox placeholder="Video Link" onChange={textHand} value={textData.videoLink} name="videoLink" type="text" />
            <Button onClick={submit} text="Submit" />
        </div>
    )
}

export default Video
