import React from 'react'
import Button from '../../components/Button/Button'
import { useHistory } from 'react-router';
import { useAuth } from '../../contexts/AuthContext';
const Home = () => {

    const history = useHistory()
    const { logout } = useAuth()


    function clClick() {
        history.push('/createlession');
    }
    function updateClick() {
        history.push('/appupdate');
    }
    function quizClick() {
        history.push('/quiz');
    }


    function videoClick() {
        history.push('/video');
    }




    const logoutClick = async () => {
        await logout()
        history.push("/")


    };
    return (
        <div className="primary-container">
            <Button onClick={clClick} text="Create Lession" />
            <Button onClick={quizClick} text="Quiz" />
            <Button onClick={videoClick} text="Video" />

            <Button onClick={updateClick} text="App Update" />
            <Button onClick={logoutClick} text="Logout" />
        </div>

    )
}

export default Home
