import React, { useState } from 'react'
import Button from '../../components/Button/Button'
import "./AppUpdate.css"
import { adminFB } from '../../firebaseDatabase'
import Textbox from '../../components/Textbox/Textbox'

const AppUpdate = () => {
    const [check, setCheck] = useState(false);
    const [versionCode, setVersionCode] = useState("");

    let force = "No"


    function submit(e) {

        if (check) {
            force = "Yes"
        } else {
            force = "No"
        }

        if (versionCode !== "") {
            adminFB.ref('Update').update({

                Version: "[" + versionCode + "," + force + "]"
            });

            setVersionCode("");
            setCheck(false)

        } else {
            alert("Please enter version code")
        }



    }
    function jj(e) {

        setVersionCode(e.target.value);

    }
    function checkbox(e) {
        setCheck(e.target.checked)

    }
    function clickf() {
        setCheck(!check)

    }

    return (
        <div className="primary-container">
            <Textbox placeholder="Version Code" type="number" onChange={jj} value={versionCode} />

            <span className="container" ><input checked={check} className="checkbox" type="checkbox" onChange={checkbox} name="force" id="" />
                <span onClick={clickf}> Force Update</span></span> <br />

            <Button onClick={submit} text="Submit" />

        </div >
    )
}

export default AppUpdate
